___CSS_LOADER_EXPORT___.push([module.id, `:root {
	--cell-size: 32px;
	--grid-gap: 4px;
	--board-padding: 0.75rem;
	--board-border: 0.5rem;

	--app-bg: hsl(0deg 0% 10%);
	--board-bg: hsl(0deg 0% 15%);
	--cell-bg: hsl(0deg 0% 30%);
	--text-secondary: hsl(0deg 0% 50%);
	--text-primary: hsl(0deg 0% 90%);

	--red: hsl(0deg 90% 60%);
	--dark-red: hsl(0deg 70% 40%);
	--neon-green: hsl(100deg 100% 70%);
}

body {
	background: var(--app-bg);
	width: 100vw;
	height: 100vh;
	font-size: 16px;
	display: flex;
	padding: 0 20vw;
	align-items: center;
	justify-content: space-between;
	font-family: 'Pixelify Sans', sans-serif;
	font-style: normal;
	font-weight: 400;
	color: var(--text-primary);
}

label {
	font-size: 2rem;
}

input {
	min-width: 300px;
	background: none;
	font-size: 2rem;
	border: 2px solid var(--app-bg);
	color: var(--app-bg);

	&:focus {
		outline: 2px solid white;
	}
}

.board-section {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 2rem;
}

.board-owner {
	font-size: 3rem;
	font-weight: 400;
	max-width: calc(var(--cell-size) * 10 + var(--grid-gap) * 9 + var(--board-padding) * 2 + var(--board-border) * 2);
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	text-align: center;
}

.ghost {
	opacity: 0.5;
	pointer-events: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;CACC,iBAAiB;CACjB,eAAe;CACf,wBAAwB;CACxB,sBAAsB;;CAEtB,0BAA0B;CAC1B,4BAA4B;CAC5B,2BAA2B;CAC3B,kCAAkC;CAClC,gCAAgC;;CAEhC,wBAAwB;CACxB,6BAA6B;CAC7B,kCAAkC;AACnC;;AAEA;CACC,yBAAyB;CACzB,YAAY;CACZ,aAAa;CACb,eAAe;CACf,aAAa;CACb,eAAe;CACf,mBAAmB;CACnB,8BAA8B;CAC9B,wCAAwC;CACxC,kBAAkB;CAClB,gBAAgB;CAChB,0BAA0B;AAC3B;;AAEA;CACC,eAAe;AAChB;;AAEA;CACC,gBAAgB;CAChB,gBAAgB;CAChB,eAAe;CACf,+BAA+B;CAC/B,oBAAoB;;CAEpB;EACC,wBAAwB;CACzB;AACD;;AAEA;CACC,kBAAkB;CAClB,aAAa;CACb,sBAAsB;CACtB,mBAAmB;CACnB,SAAS;AACV;;AAEA;CACC,eAAe;CACf,gBAAgB;CAChB,iHAAiH;CACjH,gBAAgB;CAChB,mBAAmB;CACnB,uBAAuB;CACvB,kBAAkB;AACnB;;AAEA;CACC,YAAY;CACZ,oBAAoB;AACrB","sourcesContent":[":root {\n\t--cell-size: 32px;\n\t--grid-gap: 4px;\n\t--board-padding: 0.75rem;\n\t--board-border: 0.5rem;\n\n\t--app-bg: hsl(0deg 0% 10%);\n\t--board-bg: hsl(0deg 0% 15%);\n\t--cell-bg: hsl(0deg 0% 30%);\n\t--text-secondary: hsl(0deg 0% 50%);\n\t--text-primary: hsl(0deg 0% 90%);\n\n\t--red: hsl(0deg 90% 60%);\n\t--dark-red: hsl(0deg 70% 40%);\n\t--neon-green: hsl(100deg 100% 70%);\n}\n\nbody {\n\tbackground: var(--app-bg);\n\twidth: 100vw;\n\theight: 100vh;\n\tfont-size: 16px;\n\tdisplay: flex;\n\tpadding: 0 20vw;\n\talign-items: center;\n\tjustify-content: space-between;\n\tfont-family: 'Pixelify Sans', sans-serif;\n\tfont-style: normal;\n\tfont-weight: 400;\n\tcolor: var(--text-primary);\n}\n\nlabel {\n\tfont-size: 2rem;\n}\n\ninput {\n\tmin-width: 300px;\n\tbackground: none;\n\tfont-size: 2rem;\n\tborder: 2px solid var(--app-bg);\n\tcolor: var(--app-bg);\n\n\t&:focus {\n\t\toutline: 2px solid white;\n\t}\n}\n\n.board-section {\n\tposition: relative;\n\tdisplay: flex;\n\tflex-direction: column;\n\talign-items: center;\n\tgap: 2rem;\n}\n\n.board-owner {\n\tfont-size: 3rem;\n\tfont-weight: 400;\n\tmax-width: calc(var(--cell-size) * 10 + var(--grid-gap) * 9 + var(--board-padding) * 2 + var(--board-border) * 2);\n\toverflow: hidden;\n