___CSS_LOADER_EXPORT___.push([module.id, `.ship-menu {
	display: flex;
	flex-flow: row wrap;
	gap: 1rem;

	.ship-button {
		display: flex;
		flex-direction: column;
		align-items: center;
		border-radius: 0.5rem;
		background: var(--board-bg);
		border: none;
		padding: 0.5rem 2rem;
		color: var(--text-secondary);
		cursor: pointer;

		* {
			user-select: none;
			pointer-events: none;
		}

		&:hover {
			color: var(--text-primary);
		}
		
		&:focus {
			outline: 4px solid white;
			outline-offset: -4px;
		}

		&.selected {
			background: var(--cell-bg);
			color: var(--text-primary);
		}
	}
}`, "",{"version":3,"sources":["webpack://./src/components/ship-menu/index.css"],"names":[],"mappings":"AAAA;CACC,aAAa;CACb,mBAAmB;CACnB,SAAS;;CAET;EACC,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,qBAAqB;EACrB,2BAA2B;EAC3B,YAAY;EACZ,oBAAoB;EACpB,4BAA4B;EAC5B,eAAe;;EAEf;GACC,iBAAiB;GACjB,oBAAoB;EACrB;;EAEA;GACC,0BAA0B;EAC3B;;EAEA;GACC,wBAAwB;GACxB,oBAAoB;EACrB;;EAEA;GACC,0BAA0B;GAC1B,0BAA0B;EAC3B;CACD;AACD","sourcesContent":[".ship-menu {\n\tdisplay: flex;\n\tflex-flow: row wrap;\n\tgap: 1rem;\n\n\t.ship-button {\n\t\tdisplay: flex;\n\t\tflex-direction: column;\n\t\talign-items: center;\n\t\tborder-radius: 0.5rem;\n\t\tbackground: var(--board-bg);\n\t\tborder: none;\n\t\tpadding: 0.5rem 2rem;\n\t\tcolor: var(--text-secondary);\n\t\tcursor: pointer;\n\n\t\t* {\n\t\t\tuser-select: none;\n\t\t\tpointer-events: none;\n\t\t}\n\n\t\t&:hover {\n\t\t\tcolor: var(--text-primary);\n\t\t}\n\t\t\n\t\t&:focus {\n\t\t\toutline: 4px solid white;\n\t\t\toutline-offset: -4px;\n\t\t}\n\n\t\t&.selected {\n\t\t\tbackground: var(--cell-bg);\n\t\t\tcolor: var(--text-primary);\n\t\t}\n\t}\n}"],"sourceRoot":""}]);
